@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&family=Outfit:wght@400;600;700&display=swap');
:root {
  --color-primary: #1565C0;
  --color-secondary: #1976D2;
  --color-accent: #D32F2F;
  --color-highlight: #0D47A1;
  --color-bg: #FFFFFF;
  --color-bg-alt: #F5F9FF;
  --color-surface: #FFFFFF;
  --color-text-main: #263238;
  --color-text-dim: #607D8B;
  --color-glass: rgba(21, 101, 192, 0.05);
  --color-border: rgba(21, 101, 192, 0.15);
  --color-border-hover: rgba(21, 101, 192, 0.3);
  --font-heading: 'Outfit', sans-serif;
  --font-body: 'Inter', sans-serif;
  --transition-smooth: 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  --transition-fast: 0.15s ease-out;
  --shadow-subtle: 0 2px 8px rgba(0, 0, 0, 0.08);
  --shadow-card: 0 4px 16px rgba(21, 101, 192, 0.1);
  --shadow-glow: 0 0 20px rgba(21, 101, 192, 0.15);
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  background-color: var(--color-bg);
  color: var(--color-text-main);
  font-family: var(--font-body);
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  overflow-x: hidden;
}
h1, h2, h3, h4 {
  font-family: var(--font-heading);
  letter-spacing: -0.02em;
}
.glass {
  background: var(--color-surface);
  border: 1px solid var(--color-border);
  box-shadow: var(--shadow-subtle);
}
.text-gradient {
  background: linear-gradient(90deg, var(--color-primary), var(--color-highlight));
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}
html {
  scroll-behavior: smooth;
}
body {
  background-color: var(--color-bg);
  color: var(--color-text-main);
  min-height: 100vh;
}
::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-track {
  background: var(--color-bg);
}
::-webkit-scrollbar-thumb {
  background: var(--color-border);
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: var(--color-surface);
}